import React from "react"
import { graphql } from "gatsby"
import Layout from "../components/layout"


class PlayDemo extends React.Component {

  render() {
    return <h4>Play, {this.props.name}</h4>;

  }
}
/*		
<iframe src="/bin/webgl/index.html" title="dummy" style={{width:'400px',height:'300px'}}></iframe>
*/

export default ({ data }) => {

//console out the data youâ€™re getting from the GraphQL query 
// so you can explore the data in your browser tools > console
  console.log(data)
  
  return (
    <Layout>
      <div>
        <p>My Site's Files</p>

	<PlayDemo name="Capsule"/>

        <table>
          <thead>
            <tr>
              <th>relativePath</th>
              <th>prettySize</th>
              <th>extension</th>
              <th>birthTime</th>
            </tr>
          </thead>
          <tbody>
            {data.allFile.edges.map(({ node }, index) => (
              <tr key={index}>
                <td>{node.relativePath}</td>
                <td>{node.prettySize}</td>
                <td>{node.extension}</td>
                <td>{node.birthTime}</td>
              </tr>
            ))}
          </tbody>
        </table>
      </div>

    </Layout>
  )
}

export const query = graphql`
  query {
    allFile {
      edges {
        node {
          relativePath
          prettySize
          extension
          birthTime(fromNow: true)
        }
      }
    }
  }
`